import React from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    withRouter,
    RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles, Dropdown, DropdownMenuItemType, IDropdownStyles, IDropdownOption} from '@fluentui/react';
import thm from './Theme';



type FinalState = {
    roomcode: string,
    senderid: string,
    player: any,
    Weapons: string[],
    Locations: string[],
    finalWeapon: string,
    finalLocation: string,
    goHome: Function
}
const buttonStyles = {
    root: {
        width: 200,
        borderColor: thm.palette.black,
        color: thm.palette.black
    },
};

type InvestigateProps = {
    roomcode: string,
    senderid: string,
    player: any,
    Weapons: string[],
    Locations: string[],
    goHome: Function
};

export default class Final extends React.Component<InvestigateProps> {
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            roomcode: this.props.roomcode,
            senderid: this.props.senderid,
            player: this.props.player,
            Weapons: this.props.Weapons,
            Locations: this.props.Locations,
            finalLocation: "",
            finalWeapon: "",
            goHome: this.props.goHome
        }

    }

    state: FinalState;
    handleChange(event: any) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event: any) {
        fetch('https://example.com/profile', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        })
    }

    interval: any;
    componentDidMount() {

    }

    handleInvestiagte(interactable: string) {

    }

    stackStyles: IStackStyles = {
        root: {
            background: thm.palette.themeDark,
            fontFamily: "Georgia",
        },
    };
    stackItemStyles: IStackItemStyles = {
        root: {
            background: thm.palette.white,
            color: thm.palette.black,
            padding: 5,
            fontFamily: "Georgia",
        },
    };

    getWeaponDropdown = (): IDropdownOption[] => {
        var lists = this.state.Weapons.map((item) => {
            return { key: item, text: item }
        });
        return lists;
    }

    getLocationDropdown = (): IDropdownOption[] => {
        var lists = this.state.Locations.map((item) => {
            return { key: item, text: item }
        });
        return lists;
    }

    changeWeapon = (event: any) => {
        this.setState({
            finalWeapon: event.target.value
        })
    }

    changeLocation = (event: any) => {
        this.setState({
            finalLocation: event.target.value
        })
    }

    finalSubmit() {
        console.log(JSON.stringify(this.state));
        fetch('https://zebra-game-serverless.azurewebsites.net/api/FinalGuess', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                roomcode: this.state.roomcode,
                senderid: this.state.senderid,
                person: this.state.player.target,
                weapon: this.state.finalWeapon,
                location: this.state.finalLocation
            }),
        })
        this.state.goHome();
    }

    buttonStyles = {
        root: {
          width: 200,
          borderColor: thm.palette.black,
          color: thm.palette.black
        },
      };

      stackTokens: IStackTokens = { childrenGap: 20 };

    render() {
        return (
              <div>
                <Stack styles={this.stackStyles} >
                    <StackItem styles={this.stackItemStyles} >
                        <h2>Select the weapon and location where {this.state.player.target} was killed:</h2>
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <Dropdown options={this.getWeaponDropdown()} placeholder="Weapon" onChange={this.changeWeapon} />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <Dropdown options={this.getLocationDropdown()} placeholder="Location" onChange={this.changeLocation} />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <PrimaryButton styles={this.buttonStyles} text="Submit" onClick={this.finalSubmit} />
                    </StackItem>
                </Stack>
            </div>
        );
    }
}
