import React, { FormEvent } from 'react';
import { SignInState } from './GameFlowState';
import { PrimaryButton } from '@fluentui/react';
import thm from './Theme';

type WaitingState = {
  roomcode: string,
  senderid: string,
  sent: boolean
}
export type WaitingProps = {
  senderid: string,
  roomcode: string,
  switchToStarted: Function;
}

export default class Waiting extends React.Component<WaitingProps> {
  constructor(props: WaitingProps) {
    super(props);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.state = { roomcode: props.roomcode, senderid: props.senderid, sent: false }
    this.switchToStarted = props.switchToStarted;
  }

  // stuff for figuring out if game started. (would like to move to socket or whatever but meh)
  interval: any;
  componentDidMount() {
    this.interval = setInterval(
      async () => {
        var resp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/IsGameStarted?roomcode=${this.state.roomcode}`, {
          method: 'GET', // or 'PUT'
          headers: {
            'Content-Type': 'application/json',
          },
        });
        var turn = await resp.text();
        if (turn == "MainTurn") {
          this.switchToStarted();
        }
      }, 5000);
  }
  componentWillUnmount() {
    clearInterval(this.interval);
    this.interval = null;
  }


  state: WaitingState;
  switchToStarted: Function;
  handleSubmit() {
    console.log(JSON.stringify(this.state));
    fetch('https://zebra-game-serverless.azurewebsites.net/api/StartGame', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({ senderid: this.state.senderid, roomcode: this.state.roomcode }),
    })
    this.setState({
      sent: true
    })
  }
  buttonStyles = {
    root: {
      width: 200,
      borderColor: thm.palette.black,
      color: thm.palette.black
    },
  };
  render() {
    return (
      <PrimaryButton styles={this.buttonStyles} text="Everyone ready to begin?" onClick={this.handleSubmit} disabled={this.state.sent} />
    );
  }
}