import { type } from "os";
import React, { FormEvent } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    withRouter,
    RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles } from '@fluentui/react';
import thm from './Theme';

type InvestigateState = {
    roomcode: string,
    senderid: string,
    connections: string[],
    player:any,
    goHome: Function
}
const buttonStyles = {
    root: {
        width: 200,
        borderColor: thm.palette.black,
        color: thm.palette.black
    },
};

type MoveProps = {
    roomcode: string,
    senderid: string,
    player: any,
    connections: string[],
    goHome: Function
};

export default class Move extends React.Component<MoveProps> {
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            roomcode: this.props.roomcode,
            senderid: this.props.senderid,
            connections: this.props.connections,
            player: {},
            goHome: this.props.goHome
        }

    }

    state: InvestigateState;
    handleChange(event: any) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event: FormEvent<HTMLFormElement>) {
        fetch('https://example.com/profile', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        })
    }

    interval: any;
    componentDidMount() {

    }

    stackStyles: IStackStyles = {
        root: {
            background: thm.palette.themeDark,
            fontFamily: "Georgia",
        },
    };
    stackItemStyles: IStackItemStyles = {
        root: {
            background: thm.palette.white,
            color: thm.palette.black,
            padding: 5,
            fontFamily: "Georgia",
        },
    };
    render() {
        return (
            <div>
                <Stack styles={this.stackStyles}>
                    <StackItem styles={this.stackItemStyles} >
                        Select an adjacent room to travel to:
                    </StackItem>
                    {this.state.connections?.map((room: string, index: number) => {
                        return (
                            <StackItem styles={this.stackItemStyles} key={index}>
                                <DefaultButton styles={buttonStyles} text={room} onClick={() => {
                                        console.log('pushed ' + room);
                                        console.log(JSON.stringify(this.state));
                                        fetch('https://zebra-game-serverless.azurewebsites.net/api/MoveLocation', {
                                            method: 'POST', // or 'PUT'
                                            headers: {
                                                'Content-Type': 'application/json',
                                            },
                                            body: JSON.stringify({
                                                roomcode: this.state.roomcode,
                                                senderid: this.state.senderid,
                                                location: room
                                            }),
                                        })
                                        this.state.goHome();
                                    }} />
                            </StackItem>
                        )
                    })}
                </Stack>
            </div>
        );
    }
}
