import React, { FormEvent } from 'react';
import { SignInState } from './GameFlowState';
import { PrimaryButton, IStackStyles, IStackItemStyles, StackItem, Stack, TextField } from '@fluentui/react';
import thm from './Theme';

type NameFormState = {
  roomcode: string,
  name: string,
  senderid: string
}
export type NameFormProps = {
  AddUserHandler: Function,
  senderid: string
}

export class NameForm extends React.Component<NameFormProps> {
  constructor(props: NameFormProps) {
    super(props);
    this.handleNameChange = this.handleNameChange.bind(this);
    this.handleRoomCodeChange = this.handleRoomCodeChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.handleAddUser = props.AddUserHandler;
    this.state = { roomcode: "", name: "", senderid: props.senderid }
  }

  state: NameFormState;
  handleNameChange(event: any) {
    this.setState({ name: event.target.value });
  }
  handleRoomCodeChange(event: any) {
    this.setState({ roomcode: event.target.value });
  }
  handleAddUser: Function;

  handleSubmit() {
    console.log(JSON.stringify(this.state));
    fetch('https://zebra-game-serverless.azurewebsites.net/api/AddPlayer', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(this.state),
    })
    this.handleAddUser(this.state.roomcode);
  }
  buttonStyles = {
    root: {
      width: 200,
      borderColor: thm.palette.black,
      color: thm.palette.black
    },
  };
  stackStyles: IStackStyles = {
    root: {
      background: thm.palette.themeDark,
      fontFamily: "Georgia",
    },
  };
  stackItemStyles: IStackItemStyles = {
    root: {
      background: thm.palette.white,
      color: thm.palette.black,
      padding: 5,
      fontFamily: "Georgia",
    },
  };

  render() {
    return (
      <Stack styles={this.stackStyles}>
        <StackItem styles={this.stackItemStyles}>
          <div>Name:</div>
          <TextField onChange={this.handleNameChange}></TextField>
        </StackItem>
        <StackItem styles={this.stackItemStyles}>
          <div>Room Code:</div>
          <TextField onChange={this.handleRoomCodeChange}></TextField>
        </StackItem>
        <StackItem styles={this.stackItemStyles}>
          <PrimaryButton styles={this.buttonStyles} onClick={this.handleSubmit} text="Join Game" disabled={this.state.senderid === "" || this.state.roomcode === ""}/>
        </StackItem>
      </Stack>
    );
  }
}