import { NameForm, NameFormProps } from './NameForm';
import { SignInState } from './GameFlowState';
import React from 'react';
import Guid from './Guid';
import Game from './Game';
import Waiting from './Waiting';
import { Spinner, SpinnerSize } from '@fluentui/react';

type LandingPageState = {
    roomcode: string,
    name: string,
    senderid: string,
    signInState: SignInState,
    spinning: boolean,
    player: any
}

export class LandingPage extends React.Component {
    constructor(props: any) {
        super(props);
        this.handleAddUser = this.handleAddUser.bind(this);
        this.switchToStarted = this.switchToStarted.bind(this);
    }

    handleAddUser(roomcode: string) {
        this.setState({
            signInState: SignInState.WaitingToStart,
            roomcode: roomcode
        })
    }

    switchToStarted() {
        this.setState({
            signInState: SignInState.Started
        })
    }
    state: LandingPageState = {
        roomcode: "",
        name: "",
        senderid: Guid.newGuid().toString(),
        signInState: SignInState.NotJoined, // CHANGE BACK
        spinning: false,
        player: {}
    };

    getPlayer = async () => {
        if(this.state.roomcode.length == 0){
            return;
        }
        var resp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetPlayer?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
            method: 'GET', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
        });
        if (resp.ok) {
            var player = await resp.text();
            console.log(player);
            console.log("gotplayerfromloop");
            var x = JSON.parse(`${player}`);

            this.setState({
                player: x
            })
            this.forceUpdate();
        }

    }

    intervalPlayer: any;
    componentDidMount(){
        this.getPlayer();
        this.intervalPlayer = setInterval(
          this.getPlayer, 5000);
          
    }
    componentWillUnmount(){
        clearInterval(this.intervalPlayer);
        this.intervalPlayer = null;
    }

    flipSpin = () => {
        this.setState({
            spinning: !this.state.spinning
        });
        this.getPlayer();
        console.log("spin: " + this.state.spinning);
    }

    render() {
        return <div className="LandingPage">
            {this.state.signInState === SignInState.Started ?
                (this.state.spinning ?
                    <Spinner size={SpinnerSize.large} style={{
                        position: 'absolute',
                        top: '50%',
                        left: '45%'
                    }} /> :
                    <Game roomcode={this.state.roomcode} senderid={this.state.senderid} flipSpin={this.flipSpin} player={this.state.player}></Game>)
                :
                (
                    this.state.signInState ===
                        SignInState.WaitingToStart ?
                        <Waiting senderid={this.state.senderid} roomcode={this.state.roomcode} switchToStarted={this.switchToStarted}></Waiting> :
                        <NameForm AddUserHandler={this.handleAddUser} senderid={this.state.senderid}></NameForm>
                )}
        </div>
    }
}