import { type } from "os";
import React, { FormEvent } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link,
  withRouter,
  RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles } from '@fluentui/react';
import thm from './Theme';
import Target from './Target';
import Investigate from './Investigate'
import Move from "./Move";
import Clues from "./Clues";
import Buy from "./Buy";
import SpecificBuy from './SpecificBuy';
import Final from './Final'
import { runInThisContext } from "vm";

type GameState = {
  roomcode: string,
  senderid: string,
  player: any,
  interactables: string[],
  connections: string[],
  Weapons: string[],
  Locations: string[],
  People: string[]
}

const buttonStyles = {
  root: {
    width: 200,
    borderColor: thm.palette.black,
    color: thm.palette.black
  },
};

interface GameProps extends RouteComponentProps {
  roomcode: string,
  senderid: string,
  flipSpin: Function,
  player: any
};

class Game extends React.Component<GameProps, GameState> {
  constructor(props: any) {
    super(props);
    
    this.handleSubmit = this.handleSubmit.bind(this);
    this.flipSpin = this.props.flipSpin;

    this.state = {
      roomcode: this.props.roomcode,
      senderid: this.props.senderid,
      player: this.props.player,
      interactables: [],
      connections: [],
      Weapons: [],
      People: [],
      Locations: []
      };
  }

  handleSubmit(event: FormEvent<HTMLFormElement>) {
    fetch('https://example.com/profile', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(this.state),
    })
  }

  endTurn = async () => {
    console.log("turn ended");
    let x = await fetch('https://zebra-game-serverless.azurewebsites.net/api/EndTurn', {
      method: 'POST', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        senderid: this.state.senderid,
        roomcode: this.state.roomcode
      }),
    })
  }



  getInteractables = async () => {
    var resp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetInteractables?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
      method: 'GET', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
    });
    if (resp.ok) {
      var interactables = await resp.text();
      console.log(interactables);
      var x = JSON.parse(`${interactables}`);
      this.setState({
        interactables: x
      })
    }
  }
  getConnections = async () => {
    var resp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetConnections?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
      method: 'GET', // or 'PUT'
      headers: {
        'Content-Type': 'application/json',
      },
    });
    if (resp.ok) {
      var connections = await resp.text();
      console.log(connections);
      var x = JSON.parse(`${connections}`);
      this.setState({
        connections: x
      })
    }
  }

  getBigThree = async () => {
    do {
      var WeaponResp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetWeapons?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
        method: 'GET', // or 'PUT'
        headers: {
          'Content-Type': 'application/json',
        },
      });
    } while (!WeaponResp.ok)
    let Weapons = await WeaponResp.text();
    console.log(Weapons);
    var x = JSON.parse(`${Weapons}`);
    this.setState({
      Weapons: x
    })

    do {
      var LocationResp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetLocations?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
        method: 'GET', // or 'PUT'
        headers: {
          'Content-Type': 'application/json',
        },
      });
    } while (!LocationResp.ok)
    let Locations = await LocationResp.text();
    console.log(Locations);
    var x = JSON.parse(`${Locations}`);
    this.setState({
      Locations: x
    })

    do {
      var PeopleResp = await fetch(`https://zebra-game-serverless.azurewebsites.net/api/GetPeople?roomcode=${this.state.roomcode}&senderid=${this.state.senderid}`, {
        method: 'GET', // or 'PUT'
        headers: {
          'Content-Type': 'application/json',
        },
      });
    } while (!PeopleResp.ok)
    let People = await PeopleResp.text();
    console.log(People);
    var x = JSON.parse(`${People}`);
    this.setState({
      People: x
    })
  }


  flipSpin: Function;
  goHome = () => {
    this.flipSpin();
    this.props.history.push('/');
    setTimeout(this.flipSpin, 2000);
  }

  // stuff for figuring out if game started. (would like to move to socket or whatever but meh)
  intervalInteractable: any;
  intervalConnections: any;
  componentDidMount() {
    this.getInteractables();
    this.getConnections();
    this.getBigThree();
    this.intervalInteractable = setInterval(
      this.getInteractables, 5000);

    this.intervalConnections = setInterval(
      this.getConnections, 5000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalInteractable);
    this.intervalInteractable = null;
    clearInterval(this.intervalConnections);
    this.intervalConnections = null;
  }

    stackStyles: IStackStyles = {
    root: {
      background: thm.palette.themeDark,
      fontFamily: "Georgia",
    },
  };
  stackItemStyles: IStackItemStyles = {
    root: {
      background: thm.palette.white,
      color: thm.palette.black,
      padding: 5,
      fontFamily: "Georgia",
    },
  };
  stackItemStylesText: IStackItemStyles = {
    root: {
      background: thm.palette.white,
      color: thm.palette.black,
      fontFamily: "Georgia",
    },
  };
  render() {
    return (
      <div>
        <Stack>
          <StackItem>
            Name: {this.state.player?.Name}
          </StackItem>
          <StackItem>
            Roomcode: {this.state.roomcode}
          </StackItem>
          <StackItem>
            Location: {this.state.player?.Location}
          </StackItem>
          <StackItem>
            Influence: {this.state.player?.influence}
          </StackItem>
        </Stack>
        <Switch>
          <Route path="/clues">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <Clues roomcode={this.state.roomcode} senderid={this.state.senderid} clues={this.state.player?.CluesGiven} player={this.state.player} goHome={this.goHome}></Clues>
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/target">
            <StackItem styles={this.stackItemStylesText}>
              <h2>Your Target is:</h2>
            </StackItem>
            <StackItem styles={this.stackItemStylesText}>
              <h2>{this.state.player.target}</h2>
            </StackItem>
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/move">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <Move roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} connections={this.state.connections} goHome={this.goHome} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/investigate">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <Investigate roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} interactables={this.state.interactables} goHome={this.goHome} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/specificnegative">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <SpecificBuy roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} clueType="SpecificNegative" goHome={this.goHome} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/investigate')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/specificpositive">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <SpecificBuy roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} clueType="SpecificPositive" goHome={this.goHome} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/investigate')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/buy">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <Buy roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} goHome={this.goHome}></Buy>
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/final">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <Final roomcode={this.state.roomcode} senderid={this.state.senderid} player={this.state.player} Weapons={this.state.Weapons} Locations={this.state.Locations} goHome={this.goHome}></Final>
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="Go Back" onClick={() => this.props.history.push('/')} />
              </StackItem>
            </Stack>
          </Route>
          <Route path="/">
            <Stack styles={this.stackStyles}>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Check Clues" onClick={() => this.props.history.push('/clues')} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Check Target" onClick={() => this.props.history.push('/target')} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Move Rooms" onClick={() => this.props.history.push('/move')} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Investigate" onClick={() => this.props.history.push('/investigate')} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Buy a Clue" onClick={() => this.props.history.push('/buy')} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <PrimaryButton styles={buttonStyles} text="End Turn" onClick={this.endTurn} />
              </StackItem>
              <StackItem styles={this.stackItemStyles}>
                <DefaultButton styles={buttonStyles} text="Final Guess" onClick={() => this.props.history.push('/final')} />
              </StackItem>
            </Stack>
          </Route>
        </Switch>
      </div>
    );
  }
}
export default withRouter(Game);