import { type } from "os";
import React, { FormEvent } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    withRouter,
    RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles } from '@fluentui/react';
import thm from './Theme';

type InvestigateState = {
    roomcode: string,
    senderid: string,
    player: any,
    goHome: Function
}
const buttonStyles = {
    root: {
        width: 200,
        borderColor: thm.palette.black,
        color: thm.palette.black
    },
};

interface MoveProps extends RouteComponentProps {
    roomcode: string,
    senderid: string,
    player: any,
    goHome: Function
};

class Buy extends React.Component<MoveProps> {
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            roomcode: this.props.roomcode,
            senderid: this.props.senderid,
            player: this.props.player,
            goHome: this.props.goHome
        }

    }

    state: InvestigateState;
    handleChange(event: any) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event: FormEvent<HTMLFormElement>) {
        fetch('https://example.com/profile', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        })
    }

    interval: any;
    clueTypes = [
        {
            type: "Negative",
            price: 3,
        },
        {
            type: "Positive",
            price: 12
        },
        {
            type: "Specific Negative",
            price: 6,
        },
        {
            type: "Specific Positive",
            price: 15
        },
    ];

    stackStyles: IStackStyles = {
        root: {
            background: thm.palette.themeDark,
            fontFamily: "Georgia",
        },
    };
    stackItemStyles: IStackItemStyles = {
        root: {
            background: thm.palette.white,
            color: thm.palette.black,
            padding: 5,
            fontFamily: "Georgia",
        },
    };
    render() {
        return (
            <div>
                <Stack styles={this.stackStyles}>
                    <StackItem styles={this.stackItemStyles} >
                        Select a type of clue to buy:
                    </StackItem>
                    <StackItem styles={this.stackItemStyles}>
                        <DefaultButton styles={buttonStyles} text={`${this.clueTypes[0].type}`} onClick={
                            () => {
                                console.log('pushed ' + this.clueTypes[0].type);
                                console.log(JSON.stringify(this.state));
                                fetch('https://zebra-game-serverless.azurewebsites.net/api/BuyClue', {
                                    method: 'POST', // or 'PUT'
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        roomcode: this.state.roomcode,
                                        senderid: this.state.senderid,
                                        cluetype: this.clueTypes[0].type
                                    }),
                                })
                                this.state.goHome();
                            }
                        } disabled={this.state.player.influence < this.clueTypes[0].price} />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles}>
                        <DefaultButton styles={buttonStyles} text={`${this.clueTypes[1].type}`} onClick={
                            () => {
                                console.log('pushed ' + this.clueTypes[1].type);
                                console.log(JSON.stringify(this.state));
                                fetch('https://zebra-game-serverless.azurewebsites.net/api/BuyClue', {
                                    method: 'POST', // or 'PUT'
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        roomcode: this.state.roomcode,
                                        senderid: this.state.senderid,
                                        cluetype: this.clueTypes[1].type
                                    }),
                                })
                                this.state.goHome();
                            }
                        } disabled={this.state.player.influence < this.clueTypes[1].price} />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles}>
                        <DefaultButton styles={buttonStyles} text={`${this.clueTypes[2].type}`} onClick={() =>
                            this.props.history.push("/specificnegative")
                        } disabled={this.state.player.influence < this.clueTypes[2].price} />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles}>
                        <DefaultButton styles={buttonStyles} text={`${this.clueTypes[3].type}`} onClick={() =>
                            this.props.history.push("/specificpositive")
                        } disabled={this.state.player.influence < this.clueTypes[3].price} />
                    </StackItem>
                </Stack>
            </div>
        );
    }
}
export default withRouter(Buy);