import { type } from "os";
import React, { FormEvent } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    withRouter,
    RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles, Icon } from '@fluentui/react';
import thm from './Theme';

type InvestigateState = {
    roomcode: string,
    senderid: string,
    clues: any[],
    player:any,
    goHome: Function
}
const buttonStyles = {
    root: {
        width: 200,
        borderColor: thm.palette.black,
        color: thm.palette.black
    },
};

type InvestigateProps = {
    roomcode: string,
    senderid: string,
    player: any,
    clues: any[],
    goHome: Function
};

export default class Clues extends React.Component<InvestigateProps> {
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            roomcode: this.props.roomcode,
            senderid: this.props.senderid,
            clues: this.props.clues,
            player: this.props.player,
            goHome: this.props.goHome
        }

    }

    state: InvestigateState;
    handleChange(event: any) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event: FormEvent<HTMLFormElement>) {
        fetch('https://example.com/profile', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        })
    }

    interval: any;
    componentDidMount() {

    }

    stackStyles: IStackStyles = {
        root: {
            background: thm.palette.themeDark,
            fontFamily: "Georgia",
        },
    };
    stackItemStyles: IStackItemStyles = {
        root: {
            background: thm.palette.white,
            color: thm.palette.black,
            padding: 5,
            fontFamily: "Georgia",
        },
    };
    render() {
        return (
            <div>
                <Stack styles={this.stackStyles}>
                    <StackItem styles={this.stackItemStyles}>
                        Clues: 
                    </StackItem>
                    {this.state.clues?.map((clue: any, index: number) => {
                        return (
                            <StackItem styles={this.stackItemStyles} key={index}>
                                {clue?.ClueType == 0? <div>`${clue?.Source}, ${clue?.Target}: X` </div>:<div>`${clue?.Source}, ${clue?.Target}: âœ“`</div>}
                            </StackItem>
                        )
                    })}
                </Stack>
            </div>
        );
    }
}
