import { type } from "os";
import React, { FormEvent } from 'react';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link,
    withRouter,
    RouteComponentProps
} from "react-router-dom";
import { DefaultButton, PrimaryButton, Stack, IStackTokens, StackItem, IStackStyles, ITheme, IStackItemStyles } from '@fluentui/react';
import thm from './Theme';

type InvestigateState = {
    roomcode: string,
    senderid: string,
    player: any,
    clueType: string,
    goHome: Function
}
const buttonStyles = {
    root: {
        width: 200,
        borderColor: thm.palette.black,
        color: thm.palette.black
    },
};

interface MoveProps {
    roomcode: string,
    senderid: string,
    player: any,
    clueType: string,
    goHome: Function
};

export default class SpecificBuy extends React.Component<MoveProps> {
    constructor(props: any) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.state = {
            roomcode: this.props.roomcode,
            senderid: this.props.senderid,
            player: this.props.player,
            clueType: this.props.clueType,
            goHome: this.props.goHome
        }

    }

    state: InvestigateState;
    handleChange(event: any) {
        this.setState({ value: event.target.value });
    }

    handleSubmit(event: FormEvent<HTMLFormElement>) {
        fetch('https://example.com/profile', {
            method: 'POST', // or 'PUT'
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify(this.state),
        })
    }

    interval: any;
    componentDidMount() {
        this.interval = setInterval(() => console.log(this.state.player), 5000);
    }

    stackStyles: IStackStyles = {
        root: {
            background: thm.palette.themeDark,
            fontFamily: "Georgia",
        },
    };
    stackItemStyles: IStackItemStyles = {
        root: {
            background: thm.palette.white,
            color: thm.palette.black,
            padding: 5,
            fontFamily: "Georgia",
        },
    };
    render() {
        return (
            <div>
                <Stack styles={this.stackStyles}>
                    <StackItem styles={this.stackItemStyles} >
                        Select a type of clue to buy:
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <DefaultButton styles={buttonStyles} text={`Weapon`} onClick={
                            () => {
                                console.log('pushed ' + "Weapon");
                                console.log(JSON.stringify(this.state));
                                fetch('https://zebra-game-serverless.azurewebsites.net/api/BuyClue', {
                                    method: 'POST', // or 'PUT'
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        roomcode: this.state.roomcode,
                                        senderid: this.state.senderid,
                                        cluetype: this.state.clueType,
                                        source: "na",
                                        sourceType: `Weapon`
                                    }),
                                })
                                this.state.goHome();
                            }
                        } />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <DefaultButton styles={buttonStyles} text={`Person`} onClick={
                            () => {
                                console.log('pushed ' + "Weapon");
                                console.log(JSON.stringify(this.state));
                                fetch('https://zebra-game-serverless.azurewebsites.net/api/BuyClue', {
                                    method: 'POST', // or 'PUT'
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        roomcode: this.state.roomcode,
                                        senderid: this.state.senderid,
                                        cluetype: this.state.clueType,
                                        source: "na",
                                        sourceType: `Person`
                                    }),
                                })
                                this.state.goHome();
                            }
                        } />
                    </StackItem>
                    <StackItem styles={this.stackItemStyles} >
                        <DefaultButton styles={buttonStyles} text={`Location`} onClick={
                            () => {
                                console.log('pushed ' + "Location");
                                console.log(JSON.stringify(this.state));
                                fetch('https://zebra-game-serverless.azurewebsites.net/api/BuyClue', {
                                    method: 'POST', // or 'PUT'
                                    headers: {
                                        'Content-Type': 'application/json',
                                    },
                                    body: JSON.stringify({
                                        roomcode: this.state.roomcode,
                                        senderid: this.state.senderid,
                                        cluetype: this.state.clueType,
                                        source: "na",
                                        sourceType: `Location`
                                    }),
                                })
                                this.state.goHome();
                            }
                        } />
                    </StackItem>
                </Stack>
            </div>
        );
    }
}
